/* stylelint-disable */

@mixin for-desktop {
  @media (max-width: (1210px)) {
    @content;
  }
}

@mixin for-desktop-2 {
  @media (max-width: (1100px)) {
    @content;
  }
}

@mixin tablet-mini {
  @media (max-width: (654px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
