.law-work {
  margin-bottom: 84px;
  &__item {
    display: flex;
    @include for-desktop{
      flex-wrap: wrap;
      justify-content: center;
    }
    @include small-tablet{
      width: 100%;
    }
  }
  &-image {
    margin-right: 20px;
  }
  &__content {
    width: 580px;
  }
  &__title {
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
    color: var(--dark-color);
    margin-bottom: 16px;
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    margin-bottom: 32px;
    color: var(--dark-color);
  }
  &__subtitle {
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
    margin-bottom: 32px;
    color: var(--dark-color);
  }
  &__text-image {
    margin-right: 12px;
  }
  &__text--green {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--green-color);
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }
}
