.header {
  background-color: var(--green-color);
  height: 206px;
  @include for-desktop-2 {
    height: 100%;
  }
  &__inner {
    display: flex;
    padding-top: 30px;
    @include for-desktop-2 {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__top {
    display: flex;
    @include for-desktop-2 {
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
    }
  }
  &__logo {
    margin-right: 70px;
    @include tablet{
      margin: 0;
    }
  }
  &__social {
    margin-right: 63px;
    @include for-desktop {
      margin-right: 33px;
    }
    @include tablet{
      margin: 0;
      margin-bottom: 30px;
    }
    &-messanger {
      &:not(:last-child) {
        margin-right: 12px;
      }
    }
  }
}
.address {
  &__box {
    display: flex;
    justify-content: center;
    margin-right: 70px;
    @include for-desktop {
      margin-right: 30px;
    }
    @include tablet{
      margin: 0;
      margin-left: 15px;
      margin-bottom: 30px;
    }
  }
  &__image {
    height: 32px;
    margin-right: 15px;
    @include tablet{
      margin: 0;
    }
  }
  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: var(--light-color);
  }
}

.phone {
  @include tablet{
    margin: 0 20px 20px 0;
  }
  &__box {
    width: 184px;
    margin-right: 70px;
    @include for-desktop {
      margin-right: 30px;
    }
    @include tablet{
      margin: 0;
    }
  }
  &__num {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: var(--light-color);
    position: relative;
    bottom: 15px;
  }
  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: var(--light-color);
    position: relative;
    bottom: 8px;
    left: 35px;
  }
}

.btn {
  border: 1px solid var(--light-color);
  box-sizing: border-box;
  border-radius: 12px;
  height: 52px;
  padding: 18px 31px 32px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: var(--yellow-color);
  @include tablet{
    margin-bottom: 20px;
  }
}

.nav {
  position: relative;
  left: 120px;
  bottom: 57px;
  &__list {
    display: flex;
    justify-content: center;
    @include for-desktop-2 {
      display: none;
    }
  }
  &__item {
    &:not(:last-child) {
      margin-right: 73px;
      @include for-desktop {
        margin-right: 53px;
      }
    }
  }
  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: var(--light-color);
  }
}
.burger{
  @include burger;
  position: absolute;
  top: 120px;
  right: 10px;
  display: none;
  @include for-desktop-2 {
    display: block;
  }
}

