.contacts {
  background-image: url(../img/contacts-bg.webp);
  width: 100%;
  height: 760px;
  background-repeat: no-repeat;
  background-size: cover;
  @include mobile{
    background-position: bottom;
  }
  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 40px;
    line-height: 44px;
    color: var(--dark-color);
    padding: 80px 0 12px 0;
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--dark-color);
    margin-bottom: 32px;
  }
  &__info-box {
    position: relative;
    left: 70px;
    bottom: 50px;
  }
  &__info {
    border-radius: 8px;
    width: 400px;
    height: 166px;
    background-color: #29a853;
    margin-top: 18px;
    padding: 32px 20px;
    @include mobile{
      width: 320px;
    }
  }
  &__num {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: var(--light-color);
    display: block;
  }
  &__schedule {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: var(--light-color);
    margin: 8px 0 16px 0;
  }
  &__callback {
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    text-decoration-line: underline;
    color: #eeece3;
  }
}

.form {
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  width: 400px;
  padding: 32px 20px;
  background: var(--light-color);
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.12);
  @include mobile{
    width: 320px;
  }
  &__input {
    border: 1px solid #cbcbcb;
    width: 100%;
    border-radius: 8px;
    margin-bottom: 16px;
    padding: 16px 12px;
    ::placeholder {
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      color: #646c75;
    }
  }
  &__btn {
    border-radius: 8px;
    background: #32ad5f;
    width: 100%;
    padding: 15px 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--light-color);
    margin-bottom: 12px;
  }
}

.custom-checkbox {
  display: flex;
  align-items: center;
  &__content {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: var(--dark-color);
    margin-left: 10px;
  }
  &__field {
    width: 24px;
    height: 24px;
    // background: #32ad5f;
  }
}
