.get-money {
  background: #f8f7f7;

  &__title {
    margin: 0;
    font-weight: 500;
    margin-bottom: 80px;
    font-size: 40px;
    line-height: 44px;

    color: var(--dark-color);
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 80px;
    @include for-desktop {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__item {
    border-radius: 8px;
    width: 380px;
    height: 235px;
    background: var(--light-color);
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.12);
    display: flex;
    margin-bottom: 80px;
    @include small-tablet {
      width: 100%;
      position: relative;
      z-index: 5;
    }
    &:not(:last-child) {
      margin-right: 20px;
    }

    &-image {
      height: 48px;
      margin: 21px 0 16px 20px;
    }

    &-image2 {
      @include small-tablet {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: -2;
      }
    }
    &-text {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: var(--dark-color);
      position: relative;
      right: 50px;
      top: 90px;
    }
  }
}
