.questions {
  margin-bottom: 80px;
  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 40px;
    line-height: 44px;
    margin: 84px 0 40px 0;
    color: var(--dark-color);
  }
}

.accordeon__list {
  background: var(--light-color);
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  &-item {
    margin-bottom: 12px;
  }
}
.accordeon-title__image {
  position: absolute;
  right: 26px;
  top: 29px;
}

.list-header {
  background: var(--light-color);
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  padding: 20px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  position: relative;
  color: var(--dark-color);
  &:not(:last-child) {
    margin-bottom: 16px;
  }
}
.list-content {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: var(--dark-color);
  background: inherit;
}
.fade-block {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(44, 54, 46, 0.69);
}
.modal-window {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.content-hidden {
  display: none;
}
.hidden {
  display: none;
}
.list-group-item {
  &:active {
    text-decoration: underline;
  }
}
[data-name] {
  cursor: pointer;
}
[data-tab] {
  cursor: pointer;
}
