.simply-terms {
  background: #f8f7f7;
  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 40px;
    line-height: 44px;
    color: var(--dark-color);
    padding: 80px 0 40px 0;
  }
  &__inner {
    display: flex;
    align-items: center;
    padding-bottom: 80px;
    @include tablet{
      flex-wrap: wrap;
      width: 100%;
      // justify-content: center;
    }
  }
  &__item--active {
    width: 380px;
    border-radius: 8px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.12);
    padding: 20px;
    margin-right: 20px;
    background-color: var(--green-color);
    color: var(--light-color);
    @include tablet{
      width: 100%;
      margin-bottom: 30px;
    }
  }
  &__item-title--active {
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
    margin-bottom: 16px;
  }
  &__item-text--active {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 73px;
  }
  &__item {
    border-radius: 8px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.12);
    background-color: var(--light-color);
    padding: 20px;
    width: 380px;
    height: 174px;
    margin-right: 20px;
    @include tablet{
      width: 100%;
      margin-bottom: 30px;
    }
    &-title {
      font-weight: 600;
      font-size: 20px;
      line-height: 130%;
      color: var(--green-color);
      margin-bottom: 16px;
    }
    &-text {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--dark-color);
    }
  }
  &__item-nav {
    width: 340px;
    @include tablet{
      width: 100%;
    }
  }
  &__item-image {
    margin-right: 12px;
  }
  &__item-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--dark-color);
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}
