.money {
  background-image: url(../img/money.webp);
  width: 100%;
  height: 754px;
  background-repeat: no-repeat;
  background-size: cover;

  &__title {
    margin: 0;
    font-weight: 600;
    font-size: 64px;
    line-height: 130%;
    color: var(--light-color);
    width: 500px;
    padding-top: 80px;
    margin-bottom: 32px;
    @include mobile{
      font-size: 40px;
      width: 320px;
    }
  }
  &__button {
    font-weight: 500;
    font-size: 16px;
    margin-bottom: 32px;
    line-height: 20px;
    &-apply {
      padding: 17px 43px;
      border-radius: 8px;
      background-color: var(--yellow-color);
      color: var(--light-color);
      margin-right: 24px;
      @include mobile{
        margin-bottom: 10px;
      }
    }
    &-calculate {
      padding: 17px 55px;
      border-radius: 8px;
      background-color: var(--light-color);
      color: var(--yellow-color);
    }
  }
  &__inner {
    display: flex;
    align-items: center;
    margin-bottom: 32px;
    @include mobile{
      flex-wrap: wrap;
    }
  }
  &__item {
    display: flex;
  }
  &__image {
    height: 48px;
    margin-right: 12px;
  }
  &__item-content {
    margin-right: 32px;
    &__title {
      margin: 0;
      font-weight: 600;
      font-size: 20px;
      line-height: 130%;
      color: var(--light-color);
    }
    &__text {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: var(--light-color);
    }
  }
}

.interest-rate {
  font-weight: 600;
  font-size: 20px;
  line-height: 130%;
  text-transform: uppercase;
  color: var(--light-color);
  margin-bottom: 80px;
}
