.bottom-nav {
  padding: 30px 0 30px 0;
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    @include tablet{
      display: none;
    }
  }
  &__item {
    &:not(:last-child) {
      margin-right: 82px;
    }
  }
  &__link{
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--dark-color);
  }
}
.bottom__burger{
  @include burger;
  position: relative;
  bottom: 80px;
  left: 1240px;
  display: none;
  @include for-desktop-2 {
    display: block;
  }
}
